@import url(https://fonts.googleapis.com/css?family=Roboto:400,300);

body {
  font-family: 'Roboto', sans-serif;
}

.loading-bro {
  margin: 50px auto;
  width: 150px;
}
.loading-bro > h1 {
  text-align: center;
  font-size: 2.5em;
  margin-bottom: 1em;
  font-weight: 300;
  color: #8E8E8E;
}

.green{
  color: #28a745
}

.red{
  color: #dc3545
}

.orange {
  color: #e2850a
}

#load {
  width: 150px;
  -webkit-animation: loading 3s linear infinite;
          animation: loading 3s linear infinite;
}
#load #loading-inner {
  stroke-dashoffset: 0;
  stroke-dasharray: 300;
  stroke-width: 10;
  stroke-miterlimit: 10;
  stroke-linecap: round;
  -webkit-animation: loading-circle 2s linear infinite;
          animation: loading-circle 2s linear infinite;
  stroke: #593196 ;
  fill: transparent;
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loading {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes loading-circle {
  0% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -600;
  }
}
@keyframes loading-circle {
  0% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -600;
  }
}

.loginPanel {
    width: 350px;
    padding: 8% 0 0;
    margin: auto;
  }

.valid {
    border-color: #28a745;
}

.alert-error{
  margin-top: 1%;
  color: #dc3545;
}

.invalid {
  border-color: #dc3545;
}
